	module.exports = {"data":{"markdownRemark":{"html":"<pre><code class=\"language-Perl\">use HDML; \n my $hdml = HDML->new; \n print $hdml->hdml( \n { -version => '3.0', -public => 1, -markable => 'true' }, \n $hdml->display( \n  { -title => 'Test Card - 1' }, \n  $hdml->action({ -type => 'accept', -task => 'gosub', -dest => '#2' }), \n  'FooFoo' \n ), \n $hdml->choice( \n  { -name => '2' }, \n  $hdml->line('BazBazBazBazBazBaz', $hdml->wrap), \n  \"Bar\\n\", \n  $hdml->ce({ -value => 'one' }, '1'), \n  $hdml->ce({ -value => 'two' }, '2'), \n  $hdml->ce({ -value => 'three' }, '3') \n ) \n ); \n</code></pre>\n<p>This module generates HDML (Handbeld Device Markup Language) dynamically with perl.</p>\n<p>You can easily create HDML as if you were generating HTML with CGI.pm.</p>\n<h2>download</h2>\n<ul>\n<li>Operating environment: perl 5.005 or later (5.6.1 or later is recommended)</li>\n<li>Final version: 0.55 (2002/2/26)</li>\n<li><a href=\"/filez/perl/HDML-0.55.ZIP\">Download</a> ([filesize] <a href=\"https://danmaq.com/filez/perl/HDML-0.55.ZIP%5B/filesize\">https://danmaq.com/filez/perl/HDML-0.55.ZIP[/filesize</a> ])</li>\n</ul>","frontmatter":{"cover":null,"date":"2002-02-21T00:00:00+09:00","strDate":"2002/2/20","title":"HDML.pm - Module for dynamically generating EZweb content"}}},"pathContext":{"path":"/en/blog/2002/02/21-hdml/","langKey":"en"}}

